  .page__body:has(.page__menu:target) .menu {
    overflow: auto;
  }
}

@media (width >= 1280px) {
  .page__body:has(.page__menu:target) {
    overflow: auto;
  }
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

@media (width >= 1280px) {
  .page__menu {
    display: none;
